.advantages {
  padding: 0 30px;
  margin-bottom: 27px;
}

.advantages__title {
  margin-bottom: 28px;
}

.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.advantages__item {
  position: relative;
  font-weight: 700;
  font-size: 17px;
  line-height: 1.18;
  padding-top: 55px;

  &:not(:last-child) {
    margin-bottom: 28px;
  }

  &--eco::before {
    content: url("../../img/icon-eco.svg");
    position: absolute;
    top: 0;
    left: 8px;
  }

  &--price::before {
    content: url("../../img/icon-price.svg");
    position: absolute;
    top: 1px;
    left: 0;
  }

  &--likes::before {
    content: url("../../img/icon-likes.svg");
    position: absolute;
    top: 6px;
    left: 7px;
  }

  &--handmade::before {
    content: url("../../img/icon-handmade.svg");
    position: absolute;
    top: 3px;
    left: -1px;
  }

  &--support::before {
    content: url("../../img/icon-support.svg");
    position: absolute;
    top: 4px;
    left: -6px;
  }

  &--package::before {
    content: url("../../img/icon-package.svg");
    position: absolute;
    top: 2px;
    left: 3px;
  }
}
